

.container{
  width: 100%;
  height: auto;
  max-width: 480px;
  min-height: 480px;
  min-width: 240px;
  background:#f5deb3;
  border-radius: 22px;
  align-items: center !important;
  align-content: center !important;
  margin-left: 33%;
  margin-top: 30px;
box-shadow: 5px 10px #d7d3d3;
}


.heading{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem;
}

.heading>h1{
  padding: 12px;
  border-radius: 20px;
  font-size: 1.5rem;
  border: 5px;
  
}

.form{
  align-items: center;
  align-content: center;
  size: 40px;
  margin-left: 120px;
  border-radius: 12px;
}


.btn{
  padding: 12px;
  border-radius: 12px;
  font-size: larger;
  font-style:italic;
  font-weight: bolder;
  background-color:black;
  color: white;
  margin-left: 18px;

}
.btn:hover{
  color:black;
  background-color: white;
  cursor: pointer;
}

input{
  padding: 14px;
  border-radius: 12px;
}